
html,
body {
  min-height: 100vh;
  width: 100vw;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  position: relative;
  color: #2c3e50;
  min-height: 100vh;
  width: 100vw;
  transform: translateZ(0);
}
.home {
  min-height: 100vh;
  width: 100vw;
  position: relative;
  background-color: rgba(255, 255, 255, 0.3);
}
.home .iphone {
    position: absolute;
    width: 26px;
    height: 26px;
    border-radius: 50%;
    background-color: rgba(255, 255, 255, 0.7);
    box-shadow: 0 0 8px 8px rgba(0, 0, 0, 0.7);
    margin: 10px;
    z-index: 9999;
}
.home .iphone ul {
      overflow: scroll;
      width: 100%;
      z-index: 99999;
      list-style: none;
      padding: 0;
      margin: 0;
      text-align: center;
      -webkit-overflow-scrolling: touch;
}
.home .iphone ul li {
        padding: 2px 0;
        height: 26px;
        line-height: 26px;
}
.home .iphone ul li:nth-child(odd) {
        background-color: rgba(255, 255, 255, 0);
}
.home .iphone ul li:nth-child(even) {
        background-color: rgba(0, 0, 0, 0.5);
        color: rgba(255, 255, 255, 0.9);
}
.home:before {
  content: '';
  position: absolute;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  background: url(../static/img/bg.png) no-repeat;
  background-size: calc(100% - 10px) calc(100% - 10px);
  background-position: center;
  -webkit-filter: blur(6px);
          filter: blur(6px);
}
.down-enter {
  transform: translate(0px, 99%);
}
.down-enter-to {
  transform: translate(0px, 0px);
}
.down-leave {
  transform: translate(0px, 0px);
}
.down-leave-to {
  transform: translate(0px, -100%);
}
.down-leave-active {
  transition: all .3s ease-out;
  position: absolute;
  width: 100%;
}
.down-enter-active {
  transition: all .3s ease-out;
  position: absolute;
  width: 100%;
}
.up-enter {
  /* opacity: 0; */
  transform: translate(0px, -100%);
}
.up-enter-to {
  /* opacity: 1; */
  transform: translate(0px, 0px);
}
.up-leave {
  /* opacity: 1; */
  transform: translate(0px, 0px);
}
.up-leave-to {
  /* opacity: 0; */
  transform: translate(0px, 100%);
}
.up-leave-active {
  transition: all .3s ease-out;
  position: absolute;
  width: 100%;
}
.up-enter-active {
  transition: all .3s ease-out;
  position: absolute;
  width: 100%;
}
.component-fade-enter-active {
  transition: opacity .3s  .2s ease;
}
.component-fade-leave-active {
  transition: opacity .1s  ease;
}
.component-fade-enter, .component-fade-leave-to {
  opacity: 0;
}
.wrap {
  height: 100vh;
  width: 100vw;
  text-align: center;
}
.wrap .title {
    text-align: center;
    background-color: rgba(0, 0, 0, 0.3);
    height: 6vh;
    line-height: 6.2vh;
    color: #f7f7f7;
    font-size: 4.26vw;
    font-weight: 700;
    margin-bottom: 3vh;
}
.wrap .key {
    color: #e60000;
}
.wrap .value {
    color: #333;
}
.wrap .item {
    width: 80vw;
    margin: 1vh auto;
    padding: 10px;
    font-size: 3.73vw;
    text-align: left;
}
.wrap .item-auto {
    display: inline-block;
    margin: 1vh auto;
    padding: 10px;
    font-size: 3.73vw;
}
.wrap .text {
    margin: 0 auto;
    font-size: 3.73vw;
    text-align: left;
}



